
html {
  width: 100%;
  height: 100%;
}
body {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin: 0px;
}
body div {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
#app {
  width: 100%;
  height: 100%;
  font-family: 'SEGOE UI', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #333;
}

.login {
  width: 100%;
  height: 100%;
  background: #FF5F6D;
  background: -webkit-gradient(linear, right top, left top, from(#FFC371), to(#FF5F6D));
  background: linear-gradient(to left, #FFC371, #FF5F6D);
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.login .form {
    width: 320px;
    height: 320px;
    background: white;
    border-radius: 10px;
    overflow: hidden;
    -webkit-transition: height 0.1s ease;
    transition: height 0.1s ease;
}
.login .form.expand {
      height: 390px;
}
.login .form .header {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      max-height: 50px;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      -webkit-box-pack: space-evenly;
          -ms-flex-pack: space-evenly;
              justify-content: space-evenly;
}
.login .form .header .tab {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        background: #eaeaea;
        cursor: pointer;
        color: #777;
}
.login .form .header .tab.active {
          background: white;
          color: #333;
}
.login .form .page {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: space-evenly;
          -ms-flex-pack: space-evenly;
              justify-content: space-evenly;
}
.login .form .page input {
        border: none;
        outline: none;
        height: 50px;
        color: #333;
        font-size: 20px;
        width: 250px;
}
.login .form .page .progress-bar {
        display: -webkit-inline-box;
        display: -ms-inline-flexbox;
        display: inline-flex;
        height: 2px;
        background: #ececec;
        overflow: hidden;
        width: 250px;
}
.login .form .page .progress-bar.valid div {
          background: #48c148;
}
.login .form .page .progress-bar div {
          height: 100%;
          width: -webkit-fit-content;
          width: -moz-fit-content;
          width: fit-content;
          background: #333;
          font-size: 20px;
          overflow: hidden;
          -webkit-transition: all .5s ease;
          transition: all .5s ease;
}
.login .form .page #signin,
      .login .form .page #signup {
        background: #333;
        width: 235px;
        padding: 10px;
        border-radius: 5px;
        margin-top: 20px;
        color: white;
        cursor: pointer;
        -webkit-transition: all 0.2s ease;
        transition: all 0.2s ease;
        pointer-events: none;
        opacity: .7;
}
.login .form .page #signin:hover,
        .login .form .page #signup:hover {
          background: #444;
}
.login .form .page #signin.available,
        .login .form .page #signup.available {
          pointer-events: all;
          opacity: 1;
}
